@import url("https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap");

* {
	margin: 0;
	padding: 0;
	text-decoration: none;
	border: none;
	outline: none;
	box-sizing: border-box;
	list-style: none;
	font-family: "Kumbh Sans";
	transition: color  250ms;
	transition: border 250ms;
}

body {
	display: flex;
	flex-direction: column;
	align-items: center;
}

#root {
	width: min(1440px, 100%);
}

nav,
nav :where(ul, div) {
	display: flex;
	align-items: center;
}

nav {
	position: relative;
	justify-content: space-between;
	height: 113px;
	padding-bottom: 7px;
	width: 100%;
	border-bottom: 1px solid #b6bcc8;
}

nav div:first-of-type:not(.cart *) img {
	margin-right: min(57.5px, 4vw);
}

nav ul {
	gap: min(2vw, 32px);
}

nav li,
nav a:visited {
	font-size: 15px;
	color: #68707d;
}

nav a:hover {
	color: #ff7d1a;
}

.cart-icon {
	position: relative;
	cursor: pointer;
}

.cart-icon p {
	width: 19px;
	height: 14px;
	background: #ff7d1a;
	border-radius: 6.5px;
	display: grid;
	place-content: center;
	position: absolute;
	top: -6px;
	left: 9px;
	font-weight: 700;
	font-size: 11px;
	color: #ffffff;
}

.cart-avatar-div {
	gap: min(3vw, 46px);
}

nav img#avatarImage {
	cursor: pointer;
	height: 50px;
	border-radius: 50%;
	border: 2px solid transparent;
}

nav img#avatarImage:hover {
	border-color: #ff7d1a;
}