main {
	display: flex;
	justify-content: space-between;
	padding: 90px 0;
}

.productImage {
	max-width: 445px;
	width: 60%;
	display: flex;
	flex-direction: column;
	gap: min(2vw, 32px);
}

.productImage img:nth-of-type(2) {
	max-width: 445px;
	width: 50vw;
	cursor: pointer;
	border-radius: 16px;
}

.mobile-button {
	display: none;
}

.productImage ul {
	display: flex;
	flex-wrap: wrap;
	gap: min(2vw, 31px);
}

.productImage li {
	cursor: pointer;
	width: 88px;
	height: 88px;
	background-size: cover;
	border-radius: 10px;
}

.productImage li:not(.productImage li.active):hover {
	opacity: 0.5;
}

.productImage .active {
	opacity: 0.6;
	border: 2px solid #ff7d1a;
}

main {
	gap: min(126px, 8.75vw);
}

main h1 {
	margin: 17px 0 33px;
	font-weight: 700;
	font-size: 43px;
	line-height: 50px;
	letter-spacing: 0.01em;
	color: #1d2025;
}

main p {
	font-size: 16px;
	letter-spacing: 0.003em;
	color: #68707d;
}

main h2 {
	font-weight: 700;
	font-size: 14px;
	letter-spacing: 0.08em;
	color: #ff7d1a;
}

.discount {
	margin: 27px 0 67px;
	width: min-content;
	display: grid;
	grid-template-columns: 1fr 1fr;
	align-items: center;
	font-weight: 700;
	font-size: 26px;
	letter-spacing: 0.09em;
	color: #1d2025;
}

.discount::after {
	content: "50%";
	font-size: 16px;
	letter-spacing: 0.04em;
	color: #ff7d1a;
	background: #fdebdf;
	border-radius: 6px;
	padding: 3px 10px 4px 8px;
}

.discount::before {
	grid-row: 2;
	content: "$250.00";
	text-decoration: line-through;
	font-weight: 700;
	font-size: 16px;
	letter-spacing: 0.02em;
	color: #b6bcc8;
}

.pseudoButton {
	width: 157px;
	height: 56px;
	display: flex;
	border-radius: 8px;
	overflow: hidden;
}

.pseudoButton,
.pseudoButton * {
	background: #f7f8fd;
}

.pseudoButton button {
	padding: 0 16px;
	cursor: pointer;
}

.pseudoButton input {
	text-align: center;
	width: 100%;
	font-weight: 700;
	font-size: 15px;
	letter-spacing: 0.03em;
	color: #1d2025;
}

.pseudoButton input::-webkit-outer-spin-button,
.pseudoButton input::-webkit-inner-spin-button {
	-webkit-appearance: none;
}

.pseudoButton input[type="number"] {
	-moz-appearance: textfield;
}

.addToCart {
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 16px;
	width: 272px;
	height: 56px;
	background: #ff7d1a;
	border-radius: 10px;

	font-weight: 700;
	font-size: 15px;
	letter-spacing: 0.03em;
	color: #ffffff;

	-webkit-box-shadow: 0px 18px 27px 7px rgba(255, 125, 26, 0.18);
	box-shadow: 0px 18px 27px 7px rgba(255, 125, 26, 0.18);
}

.row {
	display: flex;
	gap: 16px;
}

.productImageDetails {
	position: fixed;
	display: flex;
	justify-content: center;
	align-items: center;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(47, 47, 47, 0.8);
}

.productImageDetails .productImage {
	max-width: unset;
	position: relative;
}

.closeDetails {
	cursor: pointer;
	position: absolute;
	right: 28px;
	top: -28px;
}

.closeDetails:hover {
	content: url("images/icon-close-active.svg");
}

.mainControl {
	width: max-content;
	display: flex;
	align-items: center;
}

.mainImage {
	z-index: -1;
	height: clamp(100px, 70vh, 550px);
}

.mainControl button {
	cursor: pointer;
	width: 56px;
	height: 56px;
	background: #d9d9d9;
	border-radius: 28px;
	padding-top: 4px;
}

.mainControl button:hover,
.closeDetails:hover {
	transform: scale(1.2);
}

.mainControl button img {
	z-index: 2;
	width: 12px;
}

.mainControl button:first-of-type {
	margin-right: -28px;
	padding-right: 2px;
}

.mainControl button:first-of-type:hover img {
	content: url("images/icon-previous-active.svg");
}

.mainControl button:last-of-type {
	margin-left: -28px;
	padding-left: 2px;
}

.mainControl button:last-of-type:hover img {
	content: url("images/icon-next-active.svg");
}

.productImageDetails .productImage ul {
	display: flex;
	justify-content: center;
	width: 100%;
}

.cart {
	padding-bottom: 32px;
	display: flex;
	flex-direction: column;
	position: absolute;
	right: 0;
	top: 94px;
	width: 360px;
	min-height: 256px;
	background: #ffffff;
	box-shadow: 0px 5px 100px 2px rgba(0, 0, 0, 0.1);
	border-radius: 10px;
}

.empty-cart {
	padding: 0;
}

.empty-cart div {
	font-weight: 700;
	color: #68707d;
	position: inherit;
	bottom: 0;
	width: 100%;
	height: calc(100% - 69px);
	display: grid;
	place-content: center;
}

#empty-cart-text {
	color: #ff7d1a;
	border-color: #ff7d1a;
}

.cart h3 {
	padding: 24px;
	width: 100%;
	border-bottom: 1px solid #b6bcc8;
	font-weight: 700;
	font-size: 16px;
	letter-spacing: -0.01em;
	color: #1d2025;
}

.cart-item,
.cart-item div {
	justify-content: flex-start;
	width: calc(100% - 32px);
}

.cart-item {
	width: 312px;
	margin-top: 22px;
}

.cart-item:last-of-type {
	margin-bottom: 45px;
}

.cart-item img:first-of-type {
	border-radius: 4px;
	width: 50px;
	margin-right: 16px;
}

.cart-item div {
	align-items: flex-start;
	flex-direction: column;
}

.cart-item :where(h4, p) {
	font-weight: 400;
	font-size: 16px;
	letter-spacing: 0.003em;

	color: #68707d;
}

.cart-item p {
	letter-spacing: 0.02em;
}

.cart-item strong {
	margin-left: 8px;
	font-weight: 700;
	font-size: 16px;
	line-height: 20px;
	letter-spacing: 0.05em;
	color: #1d2025;
}

.cart button {
	width: 312px;
	height: 56px;
	background: #ff7d1a;
	border-radius: 9px;
	font-weight: 700;
	font-size: 16px;
	letter-spacing: -0.01em;
	color: #ffffff;
}

.delete-cart-item {
	cursor: pointer;
}

@media screen and (max-width: 1440px) {
	#root {
		padding: 0 20px;
	}
}

@media screen and (max-width: 1000px) {
	.cart-row {
		width: 100%;
	}

	.cart-row button {
		width: 100%;
	}

	.cart-row {
		flex-direction: column;
		justify-content: space-evenly;
	}
	.pseudoButton {
		width: 100%;
	}
}

@media screen and (max-width: 700px) {
	main {
		padding: 20px 0;
		width: 100%;
		flex-direction: column;
	}
	.productImage,
	.productImage img:nth-of-type(2) {
		max-width: unset;
		width: 100%;
	}

	.closeDetails {
		position: absolute;
		right: 20px;
	}

	.productImageDetails .productImage {
		align-items: center;
	}

	.productImage ul {
		width: 100%;
		justify-content: space-around;
	}

	.productImage ul li {
		width: 15%;
		height: unset;
		aspect-ratio: 1/1;
	}
	main h1 {
		font-size: 35px;
	}
	.cart-row button,
	.cart-row .pseudoButton {
		width: 50%;
	}

	.cart-row {
		flex-direction: row;
	}
}

@media screen and (max-width: 650px) {
	nav {
		height: 80px;
		padding: 0 10px;
		position: static;
	}

	.menu-blur {
		z-index: 1;
		background-color: #1d2025e3;
		width: 100%;
		height: 100%;
		position: fixed;
		bottom: 0;
		left: 0;
	}

	nav ul {
		align-items: flex-start;
		padding: 24px;
		z-index: 2;
		width: 70%;
		height: 100%;
		background-color: white;
		flex-direction: column;
		position: fixed;
		bottom: 0;
		left: 0;
		gap: 24px;
	}

	nav ul li:first-of-type {
		margin-bottom: 29px;
	}
	nav button {
		background-color: transparent;
	}

	.cart {
		right: 20px;
	}

	.productImage .Slideshow {
		padding: 0;
		max-width: unset;
		width: 100%;
		display: flex;
		overflow: auto;
		scroll-snap-type: x mandatory;
	}

	.productImage .Slideshow > img {
		border-radius: 0;
		min-width: 100%;
		scroll-snap-align: start;
	}
}

@media screen and (max-width: 500px) {
	#root {
		padding: 0;
	}

	.cart {
		z-index: 3;
		left: calc(10% / 2);
		width: 90%;
	}

	main div:last-of-type {
		padding: 20px;
	}

	.productImage,
	.productImage img {
		max-width: unset;
		width: 100%;
		border-radius: 0;
	}

	.productImage ul {
		display: none;
	}

	main div.cart-row {
		padding: 0;
		display: flex;
		flex-direction: column;
		width: 100%;
	}

	.cart-row .pseudoButton,
	.cart-row button {
		width: 100%;
	}

	main div.pseudoButton {
		padding: 0;
	}

	.discount::before {
		position: absolute;
		right: 20px;
	}

	.mobile-button {
		display: block;
		position: absolute;
		top: calc(80px + 50vw);
		width: 40px;
		height: 40px;
		border-radius: 50%;
	}
	.mobile-button:first-of-type {
		left: 16px;
	}
	.mobile-button:last-of-type {
		right: 16px;
	}

	.mobile-button img {
		padding-top: 3px;
		padding-left: 2px;
		width: 14px;
	}

	.mobile-button:first-of-type img {
		padding-right: 2px;
		padding-left: 0;
	}
}
